
.gallery-cont{
    min-width:50vw;
    min-height: 50vh;
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
}



.gallery-img{
    max-width: 80%;
    min-width: 80%;
    font-size: 200px;
    opacity: 0;
    transform: translateX(100%);
    transition: opacity 0.5s, transform 0.5s;
}

.gallery-img.visible{
    opacity: 1;
    transform: translateX(0);
}

#show-gallery{
    max-height:50vh;
    max-width:100%;
    display: flex;
    justify-content: space-evenly;
}

#show-gallery > img {
    max-width: 100%;
    min-width:70%
}


.show-gallery-grid{
    overflow-y: scroll;
    display: grid;
    overflow-x: hidden;
    grid-template-columns: repeat(2, 1fr); /* 4 columns */
    gap: 10px; /* Gap between grid items */
    padding: 15px; /* Padding around the grid */
    max-width:30%;
}


.show-gallery-grid > img{
    width:100%;

}


#bike-info > h3{
    color: var(--default-text-white)
}

#bike-info > ul{

    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 10px;
    
}



