.footer-main{
    display: flex;
    align-items: flex-start;
    padding-bottom:1.5rem;
}

.footer-section{
    max-width:20%;
    align-items: flex-start;
}

.footer-section > h3{
    display: inline-block;
    margin-top:1rem;
    vertical-align: top;
}



.footer-section  > *,
.footer-section > ul > *{
    color: var(--default-black);
    display: flex;
    font-size: .8rem;
}

#dcs-copy{
    background-color: var(--default-black);
    color: var(--default-text-white);
    padding: .3rem 0rem;
}